.page {
    font-family: Roboto, sans-serif;
    font-size: 16px;
    line-height: normal;
    color: #000000;
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: local('Roboto'), local('Roboto-Regular'),
    url(/test-library/static/media/roboto-v18-cyrillic_latin-regular.7f665209.woff2) format('woff2'),
    url(/test-library/static/media/roboto-v18-cyrillic_latin-regular.2b9dfec7.woff) format('woff');
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    src: local('Roboto Bold'), local('Roboto-Bold'),
    url(/test-library/static/media/roboto-v18-cyrillic_latin-700.21f4b44b.woff2) format('woff2'),
    url(/test-library/static/media/roboto-v18-cyrillic_latin-700.3125c8ca.woff) format('woff');
}

.header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;

    background-color: #999999;
}

.menu {
    padding: 15px 20px;
}

.menu__list {
    display: -webkit-flex;
    display: flex;
    margin: 0;
    padding: 0;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;

    list-style: none;
}

.menu__button {
    display: block;
    padding: 10px;

    background-color: rgba(255, 255, 255, 0.1);
    border: 1px solid #ffffff;
    border-radius: 2px;

    text-decoration: none;
    color: #ffffff;
}

.menu__button:hover,
.menu__button:focus {
    background-color: rgba(255, 255, 255, 0.4);
}

.root {
    padding: 15px 20px;
}

.book-edit {
    margin-bottom: 20px;

    background-color: #ffffff;
}

.book-edit__form {
    width: 100%;
    max-width: 560px;
}

.book-edit__wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.book-edit__heading {
    margin-top: 0;
}

.book-edit__fieldset {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 0 7px;
    padding: 10px 15px;
    grid-row-gap: 7px;
    row-gap: 7px;

    border: 1px solid #999999;
}

.book-edit__label {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.book-edit__input {
    width: auto;
    padding: 2px 5px;

    font: inherit;
    font-size: 16px;

    border: 1px solid #aaaaaa;
    background-color: #ffffff;

    resize: none;
}

.book-edit__input:disabled,
.book-edit__input:-moz-read-only {
    border-color: #eeeeee;
    background-color: #eeeeee;
}

.book-edit__input:disabled,
.book-edit__input:read-only {
    border-color: #eeeeee;
    background-color: #eeeeee;
}

.book-edit__input:focus:not(:-moz-read-only) {
    border-color: #000000;
    box-shadow:  0 0 3px rgba(0,0,0,0.5);
}

.book-edit__input:focus:not(:read-only) {
    border-color: #000000;
    box-shadow:  0 0 3px rgba(0,0,0,0.5);
}

.book-edit__input:invalid {
    background-color: #ffebee;
    border-color: #ef9a9a;
    color: #e57373;
    box-shadow:  0 0 3px rgba(239, 83, 80, 0.7);
}

.book-edit__buttons {
    display: -webkit-flex;
    display: flex;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;
}

.book-edit__rating {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin: 0;
}

.book-edit__stars {
    font-size: 30px;
}

.image-preview {
    width: 236px;
    height: auto;
}

.book-edit__button {
    display: block;
    padding: 10px;

    background-color: rgba(0, 0, 0, 0.05);
    border: 1px solid #000000;
    border-radius: 2px;

    font-size: 16px;
    text-decoration: none;
    color: #000000;
}

.book-edit__button:hover:not(:disabled),
.book-edit__button:focus {
    background-color: rgba(0, 0, 0, 0.1);
}

.book-edit__button:disabled {
    color: #aaaaaa;
    border-color: #aaaaaa;
}

.book {
    width: 236px;
}

.book__title {
    margin: 0 0 5px;

    font-weight: 700;
    font-size: 20px;
}

.book__rating {
    margin: 0;
    font-size: 24px;
}

.feed {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: baseline;
            align-items: baseline;
    margin: 0;
    padding: 0;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    grid-row-gap: 30px;
    row-gap: 30px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

    list-style: none;
}

