.book {
    width: 236px;
}

.book__title {
    margin: 0 0 5px;

    font-weight: 700;
    font-size: 20px;
}

.book__rating {
    margin: 0;
    font-size: 24px;
}

.feed {
    display: flex;
    align-items: baseline;
    margin: 0;
    padding: 0;
    column-gap: 30px;
    row-gap: 30px;
    flex-wrap: wrap;

    list-style: none;
}
